.NavigationItem {
  margin: 10px 0;
  box-sizing: border-box;
  display: block;
  width: 100%;
}

.NavigationItem a {
  color:#fffffe;
  font-weight: 400;
  font-size: 20px;
  text-decoration: none;
  width: 100%;
  box-sizing: border-box;
  display: block;
  margin-top: 20px;
  text-transform: uppercase;
  outline: none;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    font-weight: 700;
    color: #fffffe;
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
  }

  .NavigationItem a {
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
  }

  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    font-weight: 700;
  }
}
